import React from 'react';
import AlternateEmailIcon from '@mui/icons-material/AlternateEmail';
import { useParams } from 'react-router-dom';
import { Fab, Paper, Skeleton, Tooltip, Typography } from '@mui/material';
import { DraftsRounded } from '@mui/icons-material';
import { ShowUpAnimated } from 'components/animated/showUp';
import { BounceIn } from 'components/animated/bounceIn';
import ReplyRoundedIcon from '@mui/icons-material/ReplyRounded';

export function MessageView(props) {
  const { id } = useParams();
  const [content, setContent] = React.useState({});
  const getContent = () => {
    const target = props?.getMessageDetails(id);
    console.log(target);
    if (!target) {
      props?.retry().then((r) => {
        if (r) {
          getContent();
        } else {
          console.log('error');
        }
      });
    } else if (JSON.stringify(target) !== JSON.stringify(content)) setContent(target);
  };
  React.useEffect(() => {
    props?.onActive();
  }, []);
  React.useEffect(getContent, [content]);
  return (
    <div className="row">
      <div className="col-12">
        <Paper className="p-2">
          <div className="row">
            <div className="col-12">
              <div className="row">
                <div className="col-12 pt-3 px-3">
                  {Object.keys(content).length > 0 ? (
                    <BounceIn>
                      <Typography color="secondary" variant="h5">
                        <DraftsRounded fontSize="large" className="mx-3" />
                        {content.Title}
                      </Typography>
                    </BounceIn>
                  ) : (
                    <Skeleton variant="rectangular" width={210} height={118}></Skeleton>
                  )}
                </div>
              </div>
              {Object.keys(content).length > 0 ? (
                <>
                  <div className="row">
                    <div className="col-12 p-1 px-5">
                      <BounceIn delay={0.5}>
                        <Typography variant="body2" color="GrayText">
                          <AlternateEmailIcon className="mx-2" fontSize="small" />
                          {content?.Creator?.Username}
                        </Typography>
                      </BounceIn>
                    </div>
                  </div>
                </>
              ) : (
                <Skeleton variant="rectangular" width={210} height={118}></Skeleton>
              )}
            </div>
          </div>
          <hr />
          {Object.keys(content).length > 0 ? (
            <>
              <div className="row">
                <div className="col-12 p-4">
                  <ShowUpAnimated delay={1} up>
                    <div className="row">
                      <div className="col-12">
                        <Typography variant="body1" align="justify" className="border-end p-3">
                          {content.Description}
                        </Typography>
                      </div>
                    </div>
                    <div className="row">
                      <div className="col-12 text-start">
                        <Tooltip title={'ارسال پیام به ' + content?.Creator?.Username}>
                          <Fab
                            color="primary"
                            size="small"
                            onClick={() => {
                              props.sendDm(content?.Creator?._id);
                            }}>
                            <ReplyRoundedIcon />
                          </Fab>
                        </Tooltip>
                      </div>
                    </div>
                  </ShowUpAnimated>
                </div>
              </div>
            </>
          ) : (
            <Skeleton variant="rectangular" width={210} height={118}></Skeleton>
          )}
        </Paper>
      </div>
    </div>
  );
}
export default MessageView;
